body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite time;
  position: absolute;
  bottom: 50%;
}

.stopwatch__minutes-hand--speed-up {
  animation-duration: 600s;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite time;
  position: absolute;
  bottom: 50%;
}

.stopwatch__seconds-hand--speed-up {
  animation-duration: 10s;
}

.stopwatch__circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

@keyframes time {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
